<template>
  <transition name="slide">
    <div class="hospital-main">
      <c-nav-bar class="hospital-nav-bar">
        <i slot="left" class="nav-bar-left iconfont icon-fanhui1"></i>
        <span slot="center" class="nav-bar-center">
          资源医院
        </span>
      </c-nav-bar>
      <div class="hospital-content" ref="hospitalContent">
        <div class="first-child">
          <div v-for="(val, index) in get_hospitalList" :key="index" class="item">
            <span>
              {{ val.hospitalName }}
            </span>
            <span class="del-text" @click="delCityByCode(val.hospitalCode)">
              移除
            </span>
          </div>
        </div>
        <div class="add-area-btn" @click="addOneHospital">
          <i class="iconfont icon-iconset0186"></i> 添加资源医院
        </div>
      </div>
      <router-view></router-view>
    </div>
  </transition>
</template>
<script>
import BScroll from 'better-scroll'
import { mapGetters,mapActions } from 'vuex'
export default {
  name: 'receiptArea',
  data() {
    return {
      scroll: ''
    }
  },
  computed: {
    ...mapGetters(['get_hospitalList'])
  },
  mounted() {
    setTimeout(() => {
      console.log(this.get_hospitalList)
      this.init()
    }, 20)
  },
  methods: {
    ...mapActions(['change_receiptAreaList_delOne']),
    init() {
      this.scroll = new BScroll(this.$refs.hospitalContent, {
        probeType: 1,
        click: true,
        eventPassthrough: 'horizontal'
      })
    },
    delCityByCode(_cityCode) {
      this.$dialog.confirm({
        title: '提示',
        message: '是否移除此医院'
      }).then(() => {
        // on confirm
        this.change_receiptAreaList_delOne(_cityCode)
      }).catch(() => {
        // on cancel
      });
      console.log(_cityCode)
    },
    addOneHospital() {
      this.$router.push({
        path: '/qualification/hospital/seach/change_hospitalList_addOne'
      })
    }
  }
}
</script>
<style lang="scss" scoped>
@import '~@/css/mixin.scss';
.hospital-main {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: $background-color;
  z-index:1000;
  .hospital-nav-bar{
    background: #fff;
  }
  .hospital-content{
    position: fixed;
    top: 55px;
    bottom: 0;
    left: 0;
    right: 0;
    padding-bottom: 84px;
    .first-child {
      .item {
        height: 50px;
        display: flex;
        padding: 0 20px;
        align-items: center;
        justify-content: space-between;
        background: #fff;
        color:rgb(26, 26, 26);
        font-size: 16px;
        @include border-bottom-1px();
        &:last-child::after{
          display:none
        }
        .del-text {
          color: $color-theme;
          border: 1px solid $color-theme;
          width:60px;
          height: 26px;
          line-height: 26px;
          text-align: center;
          border-radius: 13px;
        }
      }
    }
    .add-area-btn {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 34px;
      height: 50px;
      line-height: 50px;
      text-align: center;
      font-size: 18px;
      color:#fff;
      background-image: linear-gradient(to right, $color-theme, ($color-gradient-end));
    }
  }
}
</style>
